<template>
    <PageHeader :title="t('pages.users.title')">
      
    </PageHeader>

    <div class="box">
        <Users :id="data.user?.code" @change="onSelectUser" :sort="USERS_SORT" :filters="USERS_FILTERS" />

        <div v-if="data.user?.code" class="user-detail">
        </div>
    </div>
</template>

<script setup>
    import { reactive } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';
    import Users from '@/components/blocks/Users.vue';

    import { USERS_SORT, USERS_FILTERS } from 'ðŸ”—/constants';
    
    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();

    const data = reactive({
        user: null
    });

    const onSelectUser = (user) => {
        data.user = null;
        setTimeout(() => {
            data.user = user?.code ? user : null;
        });
    }
</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
