<template>
    <div class="items-list">
        <div class="filters">
            <Field
                icon="search"
                :placeholder="t('blocks.users.search')"
                class="noerror light"
                @change="onSearchChange"
            />

            <Filters
                v-if="props.filters?.length"
                :filters="props.filters"
                @change="setFilters"
            />

            <Sort
                v-if="props.sort?.length"
                :sort="props.sort"
                :code="data.sort?.code"
                @change="setSort" />
        </div>

        <ul class="items" v-if="data.items.length">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.id }"
                @click="emit('change', item)"
            >
                <span class="user-value-preview">
                    <Img v-if="item.profileImage?.resized"
                        :preview="item.profileImage?.resized"
                        :original="item.profileImage?.original"
                        :alt="item?.firstName"
                    />
                    <span>
                        <span class="name">{{ item.firstName }}</span>
                        <span class="username">@{{ item.userName }}</span>
                    </span>
                </span>
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t('blocks.roles.load')"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.users.empty.title`)"
            :description="t(`pages.users.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive } from 'vue';
    import api from 'ðŸ”—/api';
    import Sort from 'ðŸ”—/components/ui/Sort.vue';
    import Filters from 'ðŸ”—/components/ui/Filters.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';

    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        title: { type: String, default: '' },
        sort: { type: Array, default: () => ([]) },
        filters: { type: Array, default: () => ([]) },
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 50,
        total: 0,
        sort: null,
        searchStr: null,
        filters: {},
    });

    const setFilters = (payload) => {
        data.filters = payload;
        reload();
    }

    const setSort = (item) => {
        if (data.sort?.code === item.code) {
            data.sort = null;
        } else {
            data.sort = item;
        }

        reload();
    };

    const onSearchChange = async (e) => {
        data.searchStr = e.target.value?.toLowerCase().trim() || '';
        data.skip = 0;

        clearTimeout(data.searchTimeout);

        data.searchTimeout = setTimeout(() => {
            delete data.searchTimeout;
            reload();
        }, 200);
    }

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params = {
            skip: data.skip,
            take: data.take
        };

        if (data.sort) {
            params.sort = data.sort.code;
            params.sort_dir = data.sort.direction;
        }

        if (!!data.searchStr?.trim().length) {
            params.firstName = data.searchStr.toLowerCase().trim();
        }

        if (data.filters && Object.keys(data.filters)?.length) {
            params = { ...data.filters, ...params };
        }

        const payload = await api.users().list(params) || [];

        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...payload.items);
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const updateItem = (item) => {
        console.log(item);
    }

    defineExpose({
        reload,
        updateItem
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        .items {
            li {
                .user-value-preview {
                    @include user-value-preview;
                    img {
                        width: 46px;
                        height: 46px;
                    }
                }
            }
        }
    }
</style>