import api from 'ðŸ”—/api';
import moment from 'moment';

export const USERS_FILTERS = [
    {
        code: "userName",
        type: "string",
        name: 'filter.users.default.username'
    }, {
        code: "created_date",
        type: "date",
        range: true,
        name: 'filter.users.default.created-date',
        formatter: (values) => {
            if (values?.length !== 2) {
                return {};
            }
            return {
                created_date_lte: moment(values[1]).toISOString(),
                created_date_gte: moment(values[0]).toISOString()
            };
        }
    }, {
        code: "roles",
        type: "autocomplete",
        multiple: true,
        name: 'filter.users.default.roles',
        options: [],
        onSearch: async (searchStr) => {
            return await api.roles().list({
                title: searchStr,
                skip: 0,
                take: 20
            });
        }
    }
]
